import React from 'react';
import PropTypes from 'prop-types';
import { getMediaFile } from '../../Utils/File';
import MessageStore from '../../Stores/MessageStore';
import FileStore from '../../Stores/FileStore';
import FileController from '../../Controllers/FileController';
import './MediaViewerContent.css';

class MediaViewerContent extends React.Component {

    constructor(props){
        super(props);

        const { chatId, messageId, size } = this.props;

        this.state = {
            file: getMediaFile(chatId, messageId, size)
        }
    }

    // shouldComponentUpdate(nextProps, nextState){
    //     if (nextState.file !== this.state.file){
    //         return true;
    //     }
    //
    //     return false;
    // }

    componentWillMount(){
        MessageStore.on('message_photo_changed', this.onMessagePhotoChanged)
        //FileStore.on('updateFile', this.onUpdateFile);
    }

    componentWillUnmount(){
        MessageStore.removeListener('message_photo_changed', this.onMessagePhotoChanged)
        //FileStore.removeListener('updateFile', this.onUpdateFile);
    }

    onMessagePhotoChanged = (payload) => {
        const { chatId, messageId, size } = this.props;

        if (messageId === payload.messageId){
            this.setState({ file: getMediaFile(chatId, messageId, size)})
        }
    };

    onUpdateFile = (update) => {
        const { file } = update;

        if (this.state.file
            && this.state.file.id === update.file.id){
            this.setState({ file: file });
        }
    };

    render() {
        const { file } = this.state;
        if (!file) return null;

        const blob = FileStore.getBlob(file.id);
        const src = blob ? URL.createObjectURL(blob) : null;

        return (
            <div className='media-viewer-content'>
                <img className='media-viewer-content-image' src={src} alt=''/>
            </div>
        );
    }
}

MediaViewerContent.propTypes = {
    chatId : PropTypes.number.isRequired,
    messageId : PropTypes.number.isRequired,
    size : PropTypes.number.isRequired
};

export default MediaViewerContent;